@import url("https://fonts.googleapis.com/css2?family=Pridi&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.buttons {
  justify-content: center;
  margin-top: 2rem;
}

button {
  background-color: #4605ff;
  color: white;
  border-radius: 8px;
  padding: 1rem;
  cursor: pointer;
  font-family: "Pridi", serif;
}

video {
  width: 80%;
  height: 350px;
  display: block;
  margin: 3rem auto;
  border-radius: 4px;
  transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg);
  -moz-transform: rotateY(180deg);
}

#screenshot {
  margin-top: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* width: 90%; */
}

#screenshot img {
  max-width: 100%;
  width: 75px;
  margin-right: 8px;
  border-radius: 4px;
}

